.image_wrapper>img{
    max-width: 100%;
    max-height: 100%;
    border-radius: 10px;
    cursor: pointer;
    -webkit-transition: all .5s;
    -moz-transition: all .5s;
    -ms-transition: all .5s;
    -o-transition: all .5s;
    transition: all .5s;
}
.image_wrapper>img:hover{
    box-shadow: -5px 5px 5px black;
    -webkit-transition: all .5s;
    -moz-transition: all .5s;
    -ms-transition: all .5s;
    -o-transition: all .5s;
    transition: all .5s;
}